<template>
  <div>
    <h3>添加待办项</h3>
    <div class="ad">
      <form @submit.prevent="onSubmit">
        <input type="text" v-model="title" placeholder="请添加你的待办项">
        <input type="submit" value="添加" class="btn">
      </form>
    </div>
  </div>
</template>

<script>
import {mapActions} from 'vuex'
export default {
    name:"AddTodos",
    data(){
        return{
            title:""
        }
    },
    methods:{
        ...mapActions(["addTodos"]),
        onSubmit(){
            this.addTodos(this.title);
            this.title="";
        }
    }
};
</script>





<style scoped>
h3{
    margin: 20px 0;
}
form {
  display: flex;
}
input[type="text"] {
  flex: 10;
  padding: 10px;
  background: aliceblue;
}
input[type="submit"] {
  flex: 2;

}
</style>
