<template>
  <div>
    <h1>这么多的内容</h1>
    <div class="todos">
      <div @dblclick="upTodo(todo)" :class="{ 'is-completed':todo.completed}" :key="index" v-for="(todo,index) in allTodos" class="todo" >{{todo.title}}
          <i @click="deleteTodo(todo.id)"  class="fas fa-trash-alt"></i>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Todos",
  props: {},
  computed: mapGetters(["allTodos"]),
  methods:{ 
      ...mapActions(["fetchTodos","deleteTodo","updateTodo"]),
   upTodo(todo){
    //    console.log(todo.id);
       const upTtodo ={
           id : todo.id,
           title : todo.title,
           completed : !todo.completed
       }
       this.updateTodo(upTtodo);
    }
  
    
  },
  created(){
      this.fetchTodos()
  }
};
</script>


<style scoped>
h1{
    margin: 20px 0;
}
.todos{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 1rem;
}
div .is-completed{
    background: goldenrod;
}
@media (max-width:600px) {
    .todos{
    grid-template-columns: 1fr;

    }
    
}
.todo{
    border: 1px solid #ccc;
    background: #41b883;
    padding: 1rem;
    border-radius: 5px;
    text-align: center;
    position: relative;
    cursor: pointer;
}
i{
    position: absolute;
    color: #fff;
    bottom: 10px;
    right: 10px;
    cursor: pointer;
}
</style>