import Axios from "axios";

const state = {
  todos: []
};
const getters = {
  allTodos: state => state.todos
};
const mutations = {
  setTodos: (state, n) => (state.todos = n),
  addTodos: (state, todo) => state.todos.unshift(todo),
  rmTodo:(state, id) => state.todos= state.todos.filter(todo => todo.id !==id),
  updatedTodo:(state,updateTodo)=>{
    const index = state.todos.findIndex(todo =>todo.id === updateTodo.id);
    if(index !== -1){
      state.todos.splice(index,1,updateTodo);
    }
  }
};
const actions = {
  async fetchTodos({ commit }) {
    const response = await axios.get(
      "http://jsonplaceholder.typicode.com/todos"
    );
    commit("setTodos", response.data);
  },
  async addTodos({ commit }, title) {
    const addTitle = await axios.post(
      "http://jsonplaceholder.typicode.com/todos",
      { title, completed: false }
    );
    commit("addTodos", addTitle.data);
  },
  async deleteTodo({ commit }, id) {
    await axios.delete(
      `http://jsonplaceholder.typicode.com/todos/${id}`
     
    );
    commit("rmTodo", id);
  },
  async filterTodos({ commit }, count) {
    // console.log(count);
    const response = await axios.get(
      `http://jsonplaceholder.typicode.com/todos?_limit=${count}`
 );
    commit("setTodos", response.data);
  },
  
  async updateTodo({ commit }, updateTodo) {
    // console.log(count);
    const response = await axios.put(
      `http://jsonplaceholder.typicode.com/todos/${updateTodo.id}`,updateTodo);
      commit("updatedTodo",response.data);
}
};
export default {
  state,
  getters,
  mutations,
  actions
};
